import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { useMappedImages } from "../../hooks/images";
import Img from "gatsby-image";

export default function Logo({ onGalleryImageClick }) {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativePath: {
            in: [
              "projects/fashion-digital-store/ysg_logo.png"
              "projects/fashion-digital-store/ysg_logo_1.jpg"
              "projects/fashion-digital-store/ysg_logo_2.jpg"
              "projects/fashion-digital-store/ysg_logo_3.jpg"
            ]
          }
        }
      ) {
        ...FluidImagesFragment
      }
    }
  `);
  const optimizedImages = useMappedImages(data.allFile);

  return (
    <>
      <div
        className="my-6 max-w-sm m-auto"
        onClick={() => {
          onGalleryImageClick("projects/fashion-digital-store/ysg_logo.png");
        }}
      >
        <Img
          className={"cursor-zoom-in"}
          fluid={
            optimizedImages["projects/fashion-digital-store/ysg_logo.png"]
              .childImageSharp.fluid
          }
        />
      </div>
      <div className="overflow-hidden">
        <div className="flex flex-wrap -mx-2">
          <div className="w-full px-2 py-2 sm:w-1/3">
            <video
              className="mx-auto"
              controls="controls"
              poster="/images/projects/fashion-digital-store/ysg_logo_1.jpg"
            >
              <source
                src="/images/projects/fashion-digital-store/ysg_video2.mp4"
                type="video/mp4"
              />
            </video>
          </div>
          <div
            className="w-full px-2 py-2 sm:w-1/3"
            onClick={() => {
              onGalleryImageClick(
                "projects/fashion-digital-store/ysg_logo_2.jpg"
              );
            }}
          >
            <Img
              className={"cursor-zoom-in"}
              fluid={
                optimizedImages["projects/fashion-digital-store/ysg_logo_2.jpg"]
                  .childImageSharp.fluid
              }
            />
          </div>
          <div
            className="w-full px-2 py-2 sm:w-1/3"
            onClick={() => {
              onGalleryImageClick(
                "projects/fashion-digital-store/ysg_logo_3.jpg"
              );
            }}
          >
            <Img
              className={"cursor-zoom-in"}
              fluid={
                optimizedImages["projects/fashion-digital-store/ysg_logo_3.jpg"]
                  .childImageSharp.fluid
              }
            />
          </div>
        </div>
      </div>
    </>
  );
}
